function Theme(key: string) {
  let doc = document.documentElement.style;

  switch (key) {
    case "light":
      localStorage.setItem("appTheme", "light");
      doc.setProperty("--cards", "white");
      doc.setProperty("--background", "white");
      doc.setProperty("--font", "black");
      doc.setProperty("--hoverInput", "#b7b7b7");
      break;
    case "dark":
      localStorage.setItem("appTheme", "dark");
      doc.setProperty("--cards", "rgb(45, 45, 45)");
      doc.setProperty("--background", "rgb(45, 45, 45)");
      doc.setProperty("--font", "rgb(206, 209, 208)");
      doc.setProperty("--hoverInput", "rgb(57, 57, 57)");
      break;

    default:
      break;
  }
  return;
}

export default Theme;
