import Theme from "./theme/theme";

export default function Settings() {
  return (
    <div className="devborders">
      <h1>settings</h1>
      <p>theme</p>
      <button className="me-2 btnx" onClick={() => Theme("light")}>
        light
      </button>
      <button className="btnx" onClick={() => Theme("dark")}>
        dark
      </button>
      <p>clear storage</p>
      <button
        className="me-2 btnx"
        onClick={() => {
          localStorage.removeItem("tasks");
          localStorage.removeItem("completed");
        }}
      >
        tasks
      </button>
      <button
        className="btnx"
        onClick={() => {
          localStorage.clear();
        }}
      >
        everything
      </button>
    </div>
  );
}
