import { format, formatDistanceToNowStrict, isPast, isToday } from "date-fns";
import { useDispatch } from "react-redux";
import { setID } from "../Redux/task";
import { Checkbox } from "./checkbox";
import { ITask } from "./interfaces";
import { Priority } from "./priority";

interface props {
  obj: ITask[];
  task: ITask;
  id: string;
  finishTask(taskNameToDelete: string, id: string): void;
}
var conditionalClass: string;

export function TaskBody({ task, obj, finishTask, id }: props) {
  const dispatch = useDispatch();

  const handleDeets = () => {
    dispatch(setID(id));
    console.log("TASK obj ", task);
  };

  const Due = () => {
    if (task.deadline !== "" && task.deadline !== undefined) {
      // DEADLINE TOKENS
      let M = parseInt(format(new Date(task.deadline), "MM"));
      let D = parseInt(format(new Date(task.deadline), "dd"));
      let Y = parseInt(format(new Date(task.deadline), "yyyy"));

      let dis = formatDistanceToNowStrict(new Date(Y, M - 1, D), {
        addSuffix: true,
      });
      let past = isPast(new Date(Y, M - 1, D));
      let today = isToday(new Date(Y, M - 1, D));
      // console.log(past);
      var day: string = dis;

      if (today === true) {
        day = " today";
        conditionalClass = "";
      }
      if (past === true) {
        conditionalClass = "text-danger";
      } else {
        conditionalClass = "";
      }

      return (
        <div className="littleGap">
          <p className={conditionalClass}>{day}</p>
        </div>
      );
    } else {
      return <span></span>;
    }
  };

  return (
    <>
      <div className="mt-2 taskRow" onClick={handleDeets}>
        <div className="w-100 mx-2 taskRow">
          <div className="d-flex flex-row flex-nowrap">
            <div
              className="checkBtn me-2"
              onClick={() => {
                finishTask(task.task, task.id);
              }}
            >
              <Checkbox col="#3e90cc" />
            </div>

            <p>{task.task}</p>
          </div>
          <div className="d-flex flex-row flex-nowrap align-items-center littleGap">
            <Due />
            <Priority type={task.priority} />
          </div>
        </div>
      </div>
    </>
  );
}
